@import url("https://fonts.googleapis.com/css?family=Roboto:400,500");
@include export-module('base-icons') {
    @at-root {
        @font-face {
            font-family: 'e-icons';
            font-style: normal;
            font-weight: normal;
            src: url(data:application/x-font-ttf;charset=utf-8;base64,) format("truetype");
        }
    }

    #{&} .e-icons {
        font-family: 'e-icons';
        font-style: normal;
        font-variant: normal;
        font-weight: normal;
        line-height: 1;
        speak: none;
        text-transform: none;
    }
}
@include export-module('base-core') {
    /*! common core */
    .e-control,
    .e-css  {
        font-family: $font-family;
        font-size: $font-size;
        font-weight: $font-weight;
    }

    #{&} .e-error {
        color: $error-font-color;
        font-family: $font-family;
        font-size: $font-size;
        font-weight: $font-weight;
    }

    .e-control,
    .e-control [class^='e-'],
    .e-control [class*=' e-'] {
        box-sizing: border-box;
    }

    .e-control:focus,
    .e-control *:focus {
        outline: none;
    }

    #{&} .e-rtl {
        direction: rtl;
    }

    #{&} .e-overlay {
        background-color: $overlay-bg-color;
        filter: alpha(opacity=50);
        height: 100%;
        opacity: .5;
        pointer-events: none;
        touch-action: none;
        width: 100%;
    }

    #{&} .e-hidden {
        display: none;
    }

    #{&} .e-disabled {
        background-image: none;
        cursor: default;
        filter: alpha(Opacity=35);
        opacity: .35;
    }

    #{&} .e-ul {
        list-style-type: none;
    }

    #{&} .e-prevent-select {
        user-select: none;
    }

    #{&} .e-warning {
        color: $warning-font-color;
    }

    #{&} .e-success {
        color: $success-font-color;
    }

    #{&} .e-information {
        color: $information-font-color;
    }

    #{&} .e-block-touch {
        touch-action: pinch-zoom;
    }
}

