    #{&} .e-ripple,
    #{&} .e-ripple-style {
        overflow: hidden;
        position: relative;
        transform: translate3d(0, 0, 0);
    }

$ripple-background: rgba(0, 0, 0, .1);

    #{&} .e-ripple-element,
    #{&} .e-ripple-style::after {
        background-color: $ripple-background;
        border-radius: 100%;
        overflow: hidden;
        pointer-events: none;
        position: absolute;
        transform: scale(0);
        transition: opacity, transform 0ms cubic-bezier(0, .1, .2, 1);
    }

    #{&} .e-ripple-style {
        $ripple-style-background: rgba(0, 0, 0, .3);

&::after {
     background: $ripple-style-background;
     content: '';
     display: block;
     height: 50%;
     left: 25%;
     opacity: 0;
     top: 25%;
     transform: scale(3);
     transition: transform .5s, opacity .5s;
     width: 50%;
 }

&:active::after {
     opacity: 1;
     transform: scale(0);
     transition: 0s;
 }
}
