        background-color: $pager-bg-color;
        color: $pager-icon-color;
    }

    .e-lastpage,
    .e-firstpage {
        background-color: transparent;
    }

    .e-pagercontainer {
        background-color: $pager-bg-color;
        border-color: $pager-border-color;
    }

    .e-firstpage,
    .e-prevpage,
    .e-firstpagedisabled,
    .e-prevpagedisabled,
    .e-nextpage,
