@include export-module('field-list-layout') {

    /*! field-list theme */

    #{&}.e-pivotfieldlist {
    .e-disable {
        display: none;
    }

    .e-hide {
        visibility: hidden;
    }
}

#{&}.e-pivot-formatting-dialog {
// sass-lint:disable-all
max-height: 480px !important;
    min-width: $format-dialog-width !important;
    width: $format-dialog-width !important;
// sass-lint:enable-all
   .e-format-table tr:nth-child(odd) {
td {
    padding-bottom: 10px;
}
}

.e-format-delete-icon {
    font-weight: normal;
@if ($field-list-skin == 'bootstrap4') {
// sass-lint:disable-all
color: #000;
// sass-lint:enable-all
opacity: .5;
}

&:hover {
     font-weight: 600;
@if ($field-list-skin == 'bootstrap4') {
    opacity: 1;
}
}
}

.e-format-table tr:nth-child(even) {
td {
    padding-bottom: 14px;
    padding-right: 18px;
}
}

.e-format-value-span {
    vertical-align: bottom;
}

.e-format-value1,
.e-format-value2,
.e-format-value-span {
    margin-right: 10px;
}

.e-format-color-picker {
    margin-right: 18px;
}

.e-format-inner-div {
    padding-left: 14px;
    padding-top: 14px;
}

.e-format-outer-div {
    background-color: $format-outer-fill-color;
    border: 1px solid $format-outer-border-color;
    border-radius: $format-outer-border-radius;
    margin-bottom: 14px;
}

.e-format-condition-button {
    float: left;
}

.e-format-delete-button {
    background-color: transparent;
    border-color: transparent;
    float: right;
    padding-right: 12px;
    padding-top: 12px;
}

.e-format-delete-button:hover,
.e-format-delete-button:focus,
.e-format-delete-button:active {
    background-color: transparent;
    border-color: transparent;
    box-shadow: 0 0 0 transparent;
}

.e-format-delete-icon {
    font-size: 10px;
}

.e-format-label,
.e-format-value-label {
    color: $format-label-font-color;
    font-size: $format-label-font-size;
    font-weight: $format-label-font-weight;
    opacity: $format-label-font-opacity;
}

.e-format-value-preview {
    border: 1px solid $format-preview-border-color;
    border-radius: $field-list-default-border-radius;
    height: $format-preview-height;
// sass-lint:disable-all
margin: 0 !important;
// sass-lint:enable-all
padding-left: 6px;
    padding-top: 6px;
    width: $format-preview-width;
}

.e-colorpicker-wrapper .e-split-btn-wrapper .e-split-btn .e-selected-color {
    background: none;
    margin: 0 2px;
    width: 14px;
}

.e-colorpicker-wrapper .e-split-btn-wrapper .e-btn.e-icon-btn {
@if ($field-list-skin == 'bootstrap4') {
    padding: 4px 5px;
}
}

.e-split-preview,
.e-tip-transparent {
    border: 1px solid $format-preview-border-color;
// sass-lint:disable-all
border-radius: 0 !important;
// sass-lint:enable-all
height: 40%;
    left: -3px;
    position: absolute;
    top: 16px;
    width: 150%;
}

&.e-device {
 // sass-lint:disable-all
 min-width: 320px !important;
     width: 100%  !important;
// sass-lint:enable-all

   .e-format-value1,
   .e-format-value2,
   .e-format-value-span {
       margin-right: 2px;
   }

.e-format-color-picker {
    width: 33%;
}

.e-format-delete-button {
    font-size: 12px;
    padding-right: 16px;
    padding-top: 16px;
}

.e-format-apply-button,
.e-format-cancel-button {
    max-width: 40%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
}

&.e-rtl {
.e-format-condition-button {
    float: right;

.e-add-icon {
    margin-left: auto;
}
}

.e-format-inner-div {
    padding-left: 0;
    padding-right: 14px;
}

.e-format-delete-button {
    float: left;
    padding-left: 12px;
}
}
}

