       margin-left: 10px;
   }
// sass-lint:enable-all

   .e-list-selected {
       opacity: .5;
   }

.e-selected-node {
    font-weight: $field-list-treeview-font-weight;
}

.e-list-text {
    font-family: $field-list-font-family;
}
}
