@include export-module('button-layout') {
    /*! button layout */
    #{&}.e-btn,
    .e-css.e-btn {
    // sass-lint:disable no-vendor-prefixes
    -webkit-font-smoothing: antialiased;
        border: $btn-border;
        border-radius: $btn-border-radius;
        box-sizing: border-box;
        cursor: pointer;
        display: inline-block;
        font-family: $font-family;
        font-size: $btn-font-size;
        font-weight: $btn-font-weight;
        justify-content: center;
        line-height: $btn-text-line-height;
        outline: none;
        padding: $btn-padding;
        text-align: center;
        text-decoration: none;
        text-transform: $btn-text-transform;
        user-select: none;
        vertical-align: middle;
        white-space: nowrap;
    @at-root {
    &:disabled {
        cursor: default;
    }

    &:hover,
    &:focus {
         text-decoration: none;
     }

    // sass-lint:disable no-vendor-prefixes
      &::-moz-focus-inner {
           border: 0;
           padding: 0;
       }

    & .e-btn-icon {
          display: inline-block;
          font-size: $btn-icon-font-size;
          margin-top: $btn-icon-margin-top;
          vertical-align: middle;
          width: $btn-icon-btn-width;

    &.e-icon-left {
         margin-left: $btn-icon-margin;
         width: $btn-icon-width;
        @if $skin-name == 'bootstrap4' {
            padding: 0 $btn-bigger-icon-top-bottom-padding;
        }
    }

&.e-icon-right {
     margin-right: $btn-icon-margin;
     width: $btn-icon-width;
