    width: 250px;

.e-resource-tree {
    height: 100%;
    overflow-y: auto;
}

.e-resource-tree.e-treeview .e-text-content {
    padding: 0 0 0 12px;
}
}

.e-resource-tree-popup-overlay {
&.e-enable {
     background-color: $schedule-popup-overlay-bg-color;
