     left: 0;
     opacity: .5;
     position: absolute;
     top: 0;
     width: 100%;
 }
}

.e-new-event {
    background-color: $schedule-appointment-bg-color;
    border: 1px solid $schedule-appointment-font-color;
    border-radius: 5px;
    height: 100%;
    opacity: .9;
    position: relative;
    width: 100%;
    z-index: 1;

.e-title {
    color: $schedule-appointment-font-color;
    font-size: 12px;
    line-height: 1.2;
    margin: 0;
    overflow-wrap: break-word;
    padding: 3px;
