    word-wrap: break-word;
}
}

.e-month-view .e-new-event {
    height: calc(100% - 25px);
}

.e-schedule-table {
    border: 0 none;
    border-collapse: separate;
    border-spacing: 0;
    margin: 0;
    table-layout: fixed;
    width: 100%;
}

.e-schedule-table>tbody>tr>th,
.e-schedule-table>tbody>tr>td {
    box-sizing: border-box;
    font-weight: normal;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.e-outer-table>tbody>tr>td {
    padding: 0;
    vertical-align: top;
}

.e-content-wrap {
    position: relative;

.e-day-wrapper {
    width: 100%;
}
}

.e-content-wrap,
.e-scroll-container {
    -webkit-overflow-scrolling: touch; // sass-lint:disable-line no-vendor-prefixes
overflow: auto;
}

.e-hide-childs > * {
    display: none;
}

.e-recurrence-icon,
.e-recurrence-edit-icon {
    font-size: $schedule-recurrence-font-size;
}

.e-left-icon,
.e-right-icon {
    font-size: 8px;
}

.e-m-date {
    cursor: pointer;
    font-size: 20px;

&:hover {
     text-decoration: underline;
 }
}

.e-m-day {
    font-size: 12px;
}

&.e-device {
.e-m-date {
&:hover {
     text-decoration: none;
 }
}
}

.e-block-appointment {
    background: $schedule-block-appointment-bg-color;
@if ($schedule-skin != 'bootstrap4') {
    border-radius: $schedule-appointment-border-radius;
}
color: $schedule-block-appointment-font-color;
cursor: default;
overflow: hidden;
position: absolute;

.e-subject {
    overflow: hidden;
    overflow-wrap: break-word;
    padding: 4px;
    white-space: normal;
    word-wrap: break-word;
}
}

.e-block-indicator {
    color: $schedule-block-appointment-font-color;
