}

.e-left-right-resize {
    border-radius: 50%;
    height: 100%;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 10px;
}

.e-top-handler .e-top-bottom-resize {
    margin-top: -8px;
}

.e-bottom-handler .e-top-bottom-resize {
    margin-top: 8px;
}

