@mixin user-select {
    -moz-user-select: none;
    -ms-user-select: none;
    -webkit-user-select: none;
    user-select: none;
}
@mixin visibility($value) {
    .e-checkbox-wrapper {
        visibility: $value;
    }
}
@mixin grid-no-border {
    .e-rowcell.e-lastrowcell {
        border-bottom-width: 0;
    }
}
@mixin popup-active-icon {
